// .home
.article{
	background: url(../images/bg.jpg) no-repeat 50% 50% ;
	background-size: cover;
}
.lang_menu{
	min-width: auto;
}
@include media-breakpoint-down(sm){
	.navbar-brand{
		img{
			max-width: 150px;
			display: block;
			height: auto;
		}
	}
}
.header{
	a{outline: none;}
	.navbar-toggler{
		border-color: rgba(#fff,.9);
		outline: none;
	}
	.navbar-nav{
		> li{
			font-size: 18px;
			&.active{
				> a{
					position: relative;
					@include media-breakpoint-up(md) {
						&:before{
							width: calc(100% - 30px);
							height: 2px;
							content: '';
							display: block;
							position: absolute;
							top: 32px;
							left: 50%;
							transform:translateX(-50%);
							background: #b7837d;
						}
					}
					
				}
			}
		}
	}
	.dropdown-toggle::after{
		border: 0;
		content: "\f078";
		font:12px/14px FontAwesome;
	}
}
.navbar_main{
	@include media-breakpoint-up(md) {
		border-right: 1px solid #fff;
		padding-right: 19px;
		margin-right: 16px;
	}
	@include media-breakpoint-down(sm){
		border-bottom: 1px solid #fff;
		padding-bottom: 10px;
		margin-bottom: 10px;
	}
	> li a{
		@include media-breakpoint-up(md) {
			padding-left: 15px !important;
			padding-right: 15px !important;
		}
	}
}
.lang_link{
	> a{
		position: relative;
		top: -2px;
		&:before{
			content: "\f0ac";
			font:24px/1 FontAwesome;
			margin-right: 4px;
		}
	}
}
.article_calc__text{
	color: #fff;
	h1{
		font-family: 'MyriadPro_Semibold';
	}
	p{
		font-size: 22px;
		line-height: 26px;
		padding-top: 4px;
	}
}
